@import "vars";
@import "_mixins/media";


.header {
    display: flex;
    justify-content: center;
  }
  
  .intro_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    width: 100%;
  }
  
  .container {
    max-width: 1320px;
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
  
    @media only screen and (max-width: 1400px) {
      max-width: 1140px;
    }
  
    @media only screen and (max-width: 1200px) {
      max-width: 960px;
  
      &.active {
        padding: 0;
        overflow: hidden;
      }
    }
  
    @media only screen and (max-width: 992px) {
      max-width: 720px;
    }
  
    @media only screen and (max-width: 768px) {
      max-width: 540px;
    }
  
    @media only screen and (max-width: 576px) {
      padding: 0 15px;
    }
  }
  
  .intro {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    &_menu {
      display: none;
    }
  
    &_info {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
  
      &_title {
        text-align: center;
        font-size: 70px;
        max-width: 650px;
        line-height: 80px;
        padding: 120px 0 60px;
  
        @media only screen and (max-width: 992px) {
          font-size: 60px;
          max-width: 550px;
          line-height: 60px;
        }
  
        @media only screen and (max-width: 768px) {
          font-size: 50px;
          max-width: 500px;
          line-height: 50px;
        }
  
        @media only screen and (max-width: 576px) {
          font-size: 30px;
          max-width: 400px;
          line-height: 40px;
        }
      }
    }
  
    @media only screen and (max-width: 1200px) {
      &_info.active {
        display: none;
      }
  
      &_wrapper {
        display: flex;
        justify-content: flex-start;
        align-items: center;
      }
  
      &_menu {
        display: flex;
        height: 25px;
        width: 25px;
        position: absolute;
        right: 0;
        z-index: 9999;
  
        span {
          position: absolute;
          width: 25px;
          height: 2px;
          background-color: $text;
          top: 50%;
          transition: 0.5s;
  
          &:nth-of-type(2) {
            top: calc(50% + 4px)
          }
  
          &:nth-of-type(3) {
            top: calc(50% - 4px)
          }
        }
  
        &.active {
          span {
            &:nth-of-type(1) {
              transform: rotate(1turn);
              opacity: 0;
            }
  
            &:nth-of-type(2) {
              top: 50%;
              transform: rotate(45deg);
            }
  
            &:nth-of-type(3) {
              top: 50%;
              transform: rotate(-45deg);
            }
          }
        }
      }
    }
  }
  
  .logo_wrap {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
  
    span {
      font-size: 20px;
    }
  
    img {
      padding-top: 10px;
      height: 100px;
    }
  }
  
  .btn {
    @include btn;
  }
  
  .circle {
    position: absolute;
    left: -21%;
    width: 800px;
    height: 800px;
    border-radius: 50%;
    background-color: $gray;
    z-index: -1;
    top: -52%;
  
  
    @media only screen and (max-width: 1400px) {
      left: -40%;
      top: -45%;
    }
  
    @media only screen and (max-width: 992px) {
      width: 600px;
      height: 600px;
      left: -40%;
      top: -25%;
    }
  
    @media only screen and (max-width: 768px) {
      left: -70%;
      top: -20%;
    }
  
    @media only screen and (max-width: 576px) {
      left: -65%;
      top: -30%;
    }
  
    @media only screen and (max-width: 450px) {
      width: 400px;
      height: 400px;
      left: -40%;
      top: -9%;
    }
  
    @media only screen and (max-width: 320px) {
      left: -65%;
      top: -10%;
    }
  }
  
  .intro_menu {
    @media only screen and (max-width: 576px) {
      right: 15px;
    }
  }
  
  .btn_circle::after {
    content: "●";
    font-size: 12px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 20%;
  }
  
  .nav {
  
    .list_item {
      @include middle_line;
    }
  
    .link_item {
      color: $text;
      text-decoration: none;
      position: relative;
      transition: all .666s;
  
      &:hover {
        color: black;
      }
  
      &::after,
      &::before {
        transition: all .666s;
      }
  
      &::after {
        position: absolute;
        content: "";
        bottom: 0;
        right: 0;
        left: 0;
        top: 25px;
        margin: auto;
        width: 0%;
        height: 2px;
        background-color: $black;
      }
  
      &:hover::after {
        width: 100%;
      }
    }
  
    .list_item {
      display: inline;
      
      &:not(:last-child) {
        margin-right: 15px;
      }
    }
  }
  
  .social_links_wrap {
    a:not(:last-child) {
      margin-right: 20px;
    }
  
    @media only screen and (max-width: 1200px) {
      display: flex;
      justify-content: space-around;
      transition: 0.5s;
      margin: auto;
    }
  
    @media only screen and (max-width: 576px) {
      display: none;
    }  
  }
  
  .social_link_list {
    display: flex;
  }
  
  .social_link_list_item {
    display: block;
    position: relative;
    background-color: $white;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    transition: 0.666s;
  
    &:not(:last-child) {
      margin-right: 20px;
    }
  
    &:hover {
      transform: translate(0, -10%);
      box-shadow: 0 5px 4px rgba(0, 0, 0, .5);
    }
  }
  
  .social_links_wrap ul li:nth-child(1):hover {
    background-color: #053eff;
  
    & .behanse_svg {
      fill: #fff;
    }
  }
  
  .social_links_wrap ul li:nth-child(2):hover {
    background-color: #1DA1F2;
  
    & .twitter_svg {
      fill: #fff;
    }
  }
  
  .social_links_wrap ul li:nth-child(3):hover {
    background-color: #4267B2;
  
    & .facebook_svg {
      fill: #fff;
    }
  }
  
  .social_links_wrap ul li:nth-child(4):hover {
    background-color: #C13584;
  
    & .inst_svg {
      fill: #fff;
    }
  }
  
  .social_links_wrap ul li:nth-child(5):hover {
    background-color: #E60023;
  
    .pinterest_svg {
      fill: #fff;
    }
  }
  
  .facebook_svg,
  .twitter_svg,
  .inst_svg,
  .behanse_svg,
  .pinterest_svg {
    path {
      transition: 200ms;
      transition-timing-function: ease-in-out;
    }
  }
  
  .social_link {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 25px;
  }
  
  .nav_wrap {
    margin: 0 75px 0 auto;
  
    @media only screen and (max-width: 1200px) {
      display: flex;
      width: 100%;
      height: 100%;
      justify-content: center;
      align-items: flex-start;
      position: fixed;
      top: 0;
      left: 0;
      background-color: #F7F1F1;
      transform: translateX(100%);
      transition: 0.5s;
      z-index: 9999;
  
      .nav_list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
  
        li {
          margin-top: 100px;
        }
      }
  
      &.active {
        transform: translateX(0%);
      }
    }
  
    @media only screen and (max-width: 768px) {
      transform: translateX(150%);
  
      & .nav ul {
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }